export default function LoginPage() {
	return (
		<div className="min-h-screen flex items-center justify-center bg-gradient-to-r from-[#2882FF] to-[#00BBA8] p-6">
			<div className="w-full max-w-5xl bg-white/0 md:bg-white rounded-2xl overflow-hidden shadow-2xl flex flex-col md:flex-row">
				{/* Left - branding / hero */}
				<div className="md:flex-1 bg-gradient-to-br from-[#2882FF] to-[#00BBA8] p-12 flex items-center justify-center text-white">
					<div className="max-w-xs text-center md:text-left">
						<h2 className="text-2xl font-semibold mb-4">
							welcome something or siguro
						</h2>
						<p className="opacity-90 mb-6">pic ng ambulance</p>
						<p className="opacity-90">or maybe name ng app</p>
					</div>
				</div>

				{/* Right - form */}
				<div className="md:w-[420px] w-full bg-white p-8 md:p-10 flex flex-col justify-center">
					<div className="mb-6">
						<div className="text-xs uppercase text-gray-400 mb-1">
							Welcome back
						</div>
						<h3 className="text-2xl font-semibold">Log In to your Account</h3>
					</div>

					<form
						className="space-y-4"
						action="#"
						onSubmit={(e) => e.preventDefault()}
					>
						<label className="block text-sm text-gray-600">
							<span className="text-xs text-gray-500">Username</span>
							<input
								name="username"
								className="mt-1 block w-full rounded-lg border border-gray-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#2882FF]"
								placeholder="admin1234"
								required
							/>
						</label>

						<label className="block text-sm text-gray-600">
							<span className="text-xs text-gray-500">Password</span>
							<input
								name="password"
								type="password"
								className="mt-1 block w-full rounded-lg border border-gray-300 px-3 py-2 focus:outline-none focus:ring-2 focus:ring-[#2882FF]"
								placeholder="••••••••••"
								required
							/>
						</label>

						<div className="flex items-center justify-between text-sm text-gray-500">
							<label className="flex items-center gap-2">
								<input
									type="checkbox"
									className="w-4 h-4 rounded border-gray-300"
								/>
								<span>Remember me</span>
							</label>
							<a className="text-blue-600 hover:underline" href="#">
								Forgot Password?
							</a>
						</div>

						<button
							type="submit"
							className="w-full mt-2 py-3 rounded-lg text-white font-medium bg-gradient-to-r from-[#1E74FF] to-[#0a9f9a] hover:opacity-95 transition"
						>
							CONTINUE
						</button>

						<div className="text-center text-sm text-gray-500">
							New User?{" "}
							<a className="text-blue-600 hover:underline" href="#">
								Sign up
							</a>
						</div>
					</form>
				</div>
			</div>
		</div>
	);
}
